import Vue from 'vue'
import App from './App.vue'
import Vuetify from 'vuetify'
import '@mdi/font/css/materialdesignicons.css';
import './components'
import Vuex from 'vuex'


Vue.config.productionTip = false
Vue.use(Vuetify);
Vue.use(Vuex);

new Vue({
  render: h => h(App),
  vuetify: new Vuetify({
    theme: { dark: true },
    icons: {
      iconfont: 'mdiSvg',
    },
  }),  
}).$mount('#app')

