<style scoped>
.small-font-note {
	font-size: xx-small;
}

.canvas-size {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	right: 0;
	bottom: 0;
	background-color: yellow;
}

.main-container {
	position: absolute;
	right: 5px;
	top: 5px;
	width: 250px;
}

.loading-progress {
	position: absolute;
	width: 50%;
	left: 0px;
	margin-left: 25%;
	top: 5px;
	z-index: 19 !important;
}

.time-slider {
	position: absolute;
	width: 50%;
	left: 0px;
	margin-left: 25%;
	bottom: 25px;
	z-index: 19 !important;
}

.input-width {
	width: 250px;
}

/* Transitions lag when trying to show loading progress */
.disable-transition {
	transition: none !important;
}

.left-container {
	position: absolute;
	top: 5px;
	bottom: 5px;
	left: 5px;
	width: 275px;
	overflow: scroll;
}

.progress-font {
	font-size: small;
}

.button-container {
	position: absolute;
	top: 5px;
	left: 5px;
	-webkit-transition-duration: 0.3s;
	-moz-transition-duration: 0.3s;
	-o-transition-duration: 0.3s;
	transition-duration: 0.3s;
}

.button-container-drawer {
	left: 280px;
}
</style>

<template>
	<div v-resize="resize">
		<canvas class="canvas-size" ref="gcodeViewer"></canvas>
		<div class="main-container">
			<v-row no-gutters>
				<v-col cols="12">
					<v-btn @click="chooseFile" title="Load File">
						<v-icon>mdi-upload</v-icon>Upload
					</v-btn>
					<v-btn @click="reload" class="ml-1" title="Reload With New Settings">
						<v-icon>mdi-reload</v-icon>Reload
					</v-btn>
				</v-col>
				<v-col cols="12">
					<span class="small-font-note">Reload after changing settings.</span>
				</v-col>

				<v-col cols="12">
					<v-text-field class="input-width" hint="Voxel Width" persistent-hint v-model="voxelWidth"></v-text-field>
				</v-col>
				<v-col cols="12">
					<v-text-field class="input-width" hint="Voxel Height" persistent-hint v-model="voxelHeight"></v-text-field>
				</v-col>
				<v-col cols="12">
					<v-checkbox class="input-width" hint="Feed Rate Coloring" persistent-hint v-model="feedColoring"></v-checkbox>
				</v-col>
				<v-col cols="6"></v-col>
				<v-col cols="6">
					<v-btn @click="resetCam" title="Reset Camera">
						<v-icon>mdi-camera</v-icon>
					</v-btn>
				</v-col>
			</v-row>
			<input :accept="'.g,.gcode,.gc,.gco,.nc,.ngc,.tap'" @change="fileSelected" hidden multiple ref="fileInput" type="file" />
		</div>
		<div :class="{ 'button-container-drawer': drawer }" class="button-container">
			<v-btn @click="drawer = !drawer" class="toggle-menu-button-close mb-10" color="secondary" small title="Show Tools">
				<v-icon>mdi-cog</v-icon>
			</v-btn>
		</div>
		<v-navigation-drawer :permanent="drawer" absolute v-model="drawer" width="275">
			<tool-configuration class="input-width"></tool-configuration>
		</v-navigation-drawer>
		<div class="loading-progress">
			<div></div>
			<v-progress-linear :value="loadingProgress" class="disable-transition" height="15" rounded v-show="loading">
				<span class="progress-font">{{loadingMessage}}{{loadingProgress}}%</span>
			</v-progress-linear>
		</div>

		<div class="time-slider">
			<v-row no-gutters>
				<v-col cols="10">
					<v-slider :disabled="loading" :label="filePositionString" :max="fileSize" min="0" v-model="filePosition"></v-slider>
				</v-col>
				<v-col cols="1">
					<v-btn @click="playing = !playing">
						<v-icon v-if="playing">mdi-pause</v-icon>
						<v-icon v-else>mdi-play</v-icon>
					</v-btn>
				</v-col>
				<v-col cols="1">
					<v-select :items="speeds" v-model="playbackSpeedSelection"></v-select>
				</v-col>
				<v-col cols="12">
					<span v-if="!loading">Press play or use the scrubber to go through file.</span>
				</v-col>
			</v-row>
		</div>
	</div>
</template>



<script>
'use strict';

import GCodeViewer, {Tool} from '@sindarius/gcodeviewer';
let viewer = null;

export default {
	data() {
		return {
			drawer: false,
			loading: false,
			loadingProgress: 0,
			loadingMessage: '',
			voxelWidth: 1,
			voxelHeight: 1,
			feedColoring: true,
			minColorRate: 0,
			maxColorRate: 6000,
			extruderColors: ['#00FFFF', '#FF00FF', '#FFFF00', '#000000', '#FFFFFF'],
			fileSize: 0,
			filePosition: 0,
			playing: false,
			speeds: [
				{text: '0.5x', value: 50},
				{text: '1x', value: 100},
				{text: '2x', value: 200},
				{text: '5x', value: 500},
				{text: '10x', value: 1000},
			],
			playbackSpeedSelection: 100,
			loadedTools: new Array(),
		};
	},
	computed: {
		filePositionString() {
			return `${this.filePosition}`;
		},
	},
	mounted() {
		this.playbackSpeedSelection = 100;
		viewer = new GCodeViewer(this.$refs.gcodeViewer);
		viewer.init();

		console.log(viewer);
		viewer.gcodeProcessor.loadingProgressCallback = (progress, message) => {
			this.loadingMessage = message;
			this.loadingProgress = Math.ceil(progress * 100);
			if (this.loadingProgress > 99) {
				this.loading = false;
			} else {
				this.loading = true;
			}
		};
		viewer.gcodeProcessor.tools = this.GetTool();

		viewer.setCursorVisiblity(false);
		viewer.gcodeProcessor.colorMode = 1;
		viewer.gcodeProcessor.renderMode = 5;
		viewer.gcodeProcessor.meshBreakPoint = 20000; //Number.MAX_VALUE; //We are going to overwrite the breakpoint for voxels (for now)  Voxel Engine will run it's own SPS. SPS per layer height?
		viewer.setZClipPlane(1000000, -1000000);
		setInterval(() => {
			if (this.playing) {
				this.filePosition += this.playbackSpeedSelection;
				viewer.gcodeProcessor.updateFilePosition(this.filePosition);
			}
		}, 200);
	},
	methods: {
		resize() {
			if (viewer !== null) {
				viewer.resize();
			}
		},
		async fileSelected(e) {
			this.updateViewerSettings();

			const reader = new FileReader();
			reader.addEventListener('load', async (event) => {
				const blob = event.target.result;
				viewer.gcodeProcessor.updateFilePosition(blob.length);
				await viewer.processFile(blob);
				this.fileSize = viewer.fileSize;
				//this.setGCodeValues();
			});
			this.loading = true;
			viewer.gcodeProcessor.colorMode = this.feedColoring ? 1 : 0;
			reader.readAsText(e.target.files[0]);
			e.target.value = '';
		},
		updateExtruderColor(index, value) {
			this.$set(this.extruderColors, index, value);
			viewer.saveExtruderColors(this.extruderColors);
		},
		chooseFile() {
			this.$refs.fileInput.click();
		},
		resetCam() {
			viewer.resetCamera();
		},
		async reload() {
			this.loading = true;
			this.updateViewerSettings();
			await viewer.reload();
		},
		updateViewerSettings() {
			viewer.gcodeProcessor.tools = this.GetTool();
			//viewer.saveExtruderColors(this.extruderColors);
			viewer.gcodeProcessor.voxelWidth = isNaN(this.voxelWidth) || this.voxelWidth === '' ? 3 : this.voxelWidth;
			viewer.gcodeProcessor.voxelHeight = isNaN(this.voxelHeight) || this.voxelHeight === '' ? 3 : this.voxelHeight;
			viewer.gcodeProcessor.minColorRate = isNaN(this.minColorRate) ? 0 : this.minColorRate;
			viewer.gcodeProcessor.maxColorRate = isNaN(this.maxColorRate) ? 6000 : this.maxColorRate;
			viewer.gcodeProcessor.colorMode = this.feedColoring ? 1 : 0;
			viewer.gcodeProcessor.setVoxelMode(true);
			viewer.updateRenderQuality(6);
		},
		resetExtruderColors() {
			this.extruderColors = ['#00FFFF', '#FF00FF', '#FFFF00', '#000000', '#FFFFFF'];
			viewer.saveExtruderColors(this.extruderColors);
			this.extruderColors.forEach((v, i) => {
				this.$set(this.extruderColors, i, v);
			});
			this.extruderColors = viewer.getExtruderColors();
		},
		async loadBenchy() {
			this.loading = true;
			let benchy = await fetch('./benchy_color.gcode');
			let blob = await benchy.blob();
			let text = await blob.text();
			if (blob === undefined) return;
			this.updateViewerSettings();
			viewer.gcodeProcessor.updateFilePosition(text.length);
			await viewer.processFile(text);
			this.fileSize = viewer.fileSize;
		},
		GetTool() {
			if (this.loadedTools.length === 0) {
				let toolString = window.localStorage.getItem('tools');
				if (toolString) {
					this.loadedTools.splice();
					let toolList = JSON.parse(toolString);
					toolList.forEach((t) => {
						let newTool = Tool.fromJson(t);
						this.loadedTools.push(newTool);
					});
				} else {
					for (let toolIdx = 0; toolIdx < 10; toolIdx++) {
						let tool = new Tool();
						tool.name = 'Tool #' + toolIdx;
						this.loadedTools.push(tool);
					}
				}
			}
			return this.loadedTools;
		},
		SaveTool() {
			let toolString = JSON.stringify(this.loadedTools);
			window.localStorage.setItem('tools', toolString);
		},
	},
	watch: {
		filePosition(to) {
			viewer.gcodeProcessor.updateFilePosition(to);
		},
	},
};
</script>



