<template>
   <vue-draggable-resizable ref="dragWindow" :hidden="!visible" :min-width="minWidth" :min-height="200" :w="w" :h="h" :x="x" :y="y" class="main-window" :drag-handle="dragHandle" :parent="true">
      <div class="fill-area">
         <v-toolbar class="drag-handle">
            <v-btn large @click="hide" color="error" class="close-button mr-1"><v-icon>mdi-close</v-icon></v-btn>
            <v-btn large @click="maximize" color="info" class="close-button mr-3"
               ><v-icon>{{ maximized ? `mdi-window-restore` : `mdi-window-maximize` }} </v-icon>
            </v-btn>
            <v-toolbar-title>{{ title }}</v-toolbar-title>
         </v-toolbar>
         <main class="content-area">
            <slot></slot>
         </main>
         <footer class="footer"><slot name="footer"></slot></footer>
      </div>
   </vue-draggable-resizable>
</template>

<script>
  'use strict';
  import Vue from 'vue';

  import VueDraggableResizable from 'vue-draggable-resizable';
  import 'vue-draggable-resizable/dist/VueDraggableResizable.css';

  Vue.component('vue-draggable-resizable', VueDraggableResizable);

  export default {
     props: {
        visible: {
           type: Boolean,
           required: true,
        },
        title: String,
        startfull: { type: Boolean, default: false },
        minWidth : {
           type : Number,
           default : 300
        }
     },
     data: function () {
        return {
           dragHandle: '.drag-handle',
           x: 100,
           y: 100,
           w: this.$props.minWidth,
           h: 200,
           maximized: false,
           restoreDimensions: {},
        };
     },
     methods: {
        async maximize() {
           if (this.maximized) {
              this.maximized = false;
              this.dragHandle = '.drag-handle';
              //this is broken into two ticks otherwise the window will fail position because the window is still max size
              //which causes it to sit at 0,0 after minimized;
              await this.$nextTick().then(() => {
                 this.w = this.restoreDimensions.w;
                 this.h = this.restoreDimensions.h;
              });
              await this.$nextTick().then(() => {
                 this.x = this.restoreDimensions.x;
                 this.y = this.restoreDimensions.y;
              });
              return;
           }

           this.maximized = true;
           this.dragHandle = '.drag-handle-disabled';
           this.restoreDimensions = {
              x: this.$refs.dragWindow.left,
              y: this.$refs.dragWindow.top,
              w: this.$refs.dragWindow.width,
              h: this.$refs.dragWindow.height,
           };

           this.x = 0;
           this.y = 0;
           this.w = window.innerWidth;
           this.h = window.innerHeight;
        },
        hide() {
           this.$emit('update:visible', false);
        },
     },
     watch: {
        visible: function (to) {
           if (to && this.startfull && !this.maximized) {
              this.maximize();
           }
        },
     },
  };
</script>

<style scoped>
  .fill-area {
     width: 100%;
     height: 100%;
     position: relative;
     border: 2px #ff6600 solid;
  }

  .content-area {
     position: absolute;
     top: 54px;
     bottom: 0px;
     left: 0px;
     right: 0px;
     background-color: black;
  }
  .main-window {
     z-index: 100 !important;
  }
  .footer {
     width: 100%;
     position: absolute;
     height: 0px;
     bottom: 0;
     background-color: black;
  }
  video::-webkit-media-controls-fullscreen-button {
     display: none !important;
  }
</style>