<template>
	<div class="bgcolor">
		<v-btn @click="save()" block>Save Tools</v-btn>
		<v-card :key="idx" class="mb-2" v-for="(tool, idx) in tools">
			<v-card-title>{{tool.name}}</v-card-title>
			<v-card-text>
				<v-row>
					<v-col cols="12">
						<div class="mb-2">Filamnt Color</div>
						<color-picker :editcolor.sync="tool.colorString"></color-picker>
					</v-col>
					<v-col cols="12">
						Diameter
						<v-text-field v-model="tool.diameter"></v-text-field>
					</v-col>
					<v-col cols="12">
						Tool Type
						<v-select :items="toolTypes" v-model="tool.toolType"></v-select>
					</v-col>
				</v-row>
			</v-card-text>
		</v-card>
	</div>
</template>

<script>
import {Tool} from '@sindarius/gcodeviewer';

export default {
	data() {
		return {
			toolCountValue: 1,
			tools: new Array(),
			toolTypes: ['Extruder', 'Endmill'],
			loadedTools: new Array(),
		};
	},
	mounted() {
		this.tools = this.GetTool();
	},
	methods: {
		save() {
			this.SaveTool();
		},
		GetTool() {
			if (this.loadedTools.length === 0) {
				let toolString = window.localStorage.getItem('tools');
				if (toolString) {
					this.loadedTools.splice();
					let toolList = JSON.parse(toolString);
					toolList.forEach((t) => {
						let newTool = Tool.fromJson(t);
						this.loadedTools.push(newTool);
					});
				} else {
					for (let toolIdx = 0; toolIdx < 10; toolIdx++) {
						let tool = new Tool();
						tool.name = 'Tool #' + toolIdx;
						this.loadedTools.push(tool);
					}
				}
			}
			return this.loadedTools;
		},
		SaveTool() {
			let toolString = JSON.stringify(this.loadedTools);
			window.localStorage.setItem('tools', toolString);
		},
	},
	computed: {
		toolCount: {
			get: function () {
				return this.toolCountValue;
			},
			set: function (value) {
				if (value > 10 || value < 1) {
					return;
				}
				this.toolCountValue = value;
			},
		},
	},
};
</script>

<style>
.bgcolor {
	background-color: black;
}
</style>